export function ErrorFallback({
  error,
  resetErrorBoundary,
}: { error: Error; resetErrorBoundary: () => void }) {
  return (
    <div role="alert">
      <p>:</p>
      <pre>{error.message}</pre>
      <button type="button" onClick={resetErrorBoundary}>
        
      </button>
    </div>
  );
}
